  import {
			mapState,
	} from 'vuex';
  export default {
    data(){
      return{
        list: []
      }
    },
    onShow(){
      this.sendRequest()
    },
    computed: {
			...mapState(['userInfo'])
		},
    methods: {
       async sendRequest(){
            try {
                const list = await this.$request.post('/payment/index', {
                    header:{
                      token: this.userInfo.token
                    }
                });

                this.list = list.data.data
                this.$common.checkStatus(list.data.code)
            } catch (error) {
                console.error('error:', error);
            }
        },
        toEdit(id){
          uni.navigateTo({
              url: `./alipay-edit?id=${id}`
          });
        },
        toDel(id,index){
          let _this = this
          uni.showModal({
              title: '提示',
              content: '是否要删除当前账号?',
              success: function (res) {
                  if (res.confirm) {
                    _this.delRequest(id,index)
                  } else if (res.cancel) {
                      console.log('用户点击取消');
                  }
              }
          });
        },
        async delRequest(id,index){
          let  _this = this
          try {
              const res = await _this.$request.post('/payment/remove-payment', {
                  data:{
                    Id: id
                  },
                  header:{
                    token: _this.userInfo.token
                  }
              });

